.pulsating-circle {
    position: absolute;
    left:5px;
    top: 39%;
    transform: translateX(-50%) translateY(-50%);
    width: 20px;
    height: 20px;
    /* background: radial-gradient(black, transparent); */
}
.pulsating-circle:before {
    content: "";
    position: relative;
    display: block;
    width: 200%;
    height: 200%;
    box-sizing: border-box;
    margin-left: -10px;
    margin-top: -11px;
    border-radius: 45px;
    background-color: #01a4e9;
    animation: pulse-ring 1.25s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
}

  
  .pulsating-circle:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    display: block;
    width: 100%;
    height: 100%;
    background-color: #004fff;
    border-radius: 15px;
    box-shadow: 0 0 8px rgb(0 0 0 / 30%);
    animation: pulse-dot 1.25s cubic-bezier(0.455, 0.03, 0.515, 0.955) -0.4s infinite;
}

.buy-btn {
  font-size: 18px !important;
  padding: 15px 30px !important;
  /* width: 100%; */
  font-weight: 600 !important; margin-left:25px;

}

  
  @-webkit-keyframes pulse-ring {
    0% {
      transform: scale(0.33);
    }
    80%, 100% {
      opacity: 0;
    }
  }
  
  @keyframes pulse-ring {
    0% {
      transform: scale(0.33);
    }
    80%, 100% {
      opacity: 0;
    }
  }
  @-webkit-keyframes pulse-dot {
    0% {
      transform: scale(0.8);
    }
    50% {
      transform: scale(1);
    }
    100% {
      transform: scale(0.8);
    }
  }
  @keyframes pulse-dot {
    0% {
      transform: scale(0.8);
    }
    50% {
      transform: scale(1);
    }
    100% {
      transform: scale(0.8);
    }
  }

  .pulsating-circle.red:after { background: red;}
  .pulsating-circle.red:before { background: red;}

  .pulsating-circle.grey:after { background: gray;}
  .pulsating-circle.grey:before { background: gray;}

  .pulsating-circle.sky-bl:after { background:#10dfe7;}
  .pulsating-circle.sky-bl:before { background:#10dfe7;}

