.circle-parice.section .heading-contain {margin-bottom: 60px;}
.circle-price-box.layout-circle {padding: 0 !important; padding:0 0 40px !important;overflow: hidden;}
.circle-price-box.layout-circle .price-box { background: #2C2C2C;  padding-top: 50px;  padding-bottom: 25px;  position: relative;}
.circle-price-box.layout-circle  .price-value { height: auto; width: auto; border-radius: unset;  margin: auto; background:transparent !important; color: #fff;  position: static;}
.circle-price-box.layout-circle .price-value span {position: static; transform: none; margin: auto; line-height: 1; color: #fff;}
.circle-price-box.layout-circle .price-value .free-days sub {
  font-weight: 400;
  font-size: 20px;
  display: inline;
  bottom: 0;
  vertical-align: baseline;
  line-height: 0;
}

.circle-price-box.layout-circle h4.pricing-name {
  margin-top: 10px;
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 700;
  color: #fff;
}

.circle-price-box.layout-circle .price-box:before, .circle-price-box.layout-circle .price-box:after {
  content: '';
  position: absolute;
  height: 60px;
  background-color: #FFF;
  width: 570px;
  bottom: -60px;
}

.circle-price-box.layout-circle .price-box:before {
  right: 50%;
  transform-origin: top right;
  -webkit-transform: rotate(12deg);
  -moz-transform: rotate(12deg);
  -o-transform: rotate(12deg);
  -ms-transform: rotate(12deg);
  transform: rotate(12deg);
}

.circle-price-box.layout-circle .price-box:after {
  left: 50%;
  transform-origin: top left;
  -webkit-transform: rotate(-12deg);
  -moz-transform: rotate(-12deg);
  -o-transform: rotate(-12deg);
  -ms-transform: rotate(-12deg);
  transform: rotate(-12deg);
}

