body{ box-sizing: border-box; text-decoration: none; font-family: Poppins !important; color:#191919; font-size:14px; font-weight:300; line-height: 1.714;-webkit-font-smoothing: antialiased;}
a{text-decoration: none !important;}
*{margin: 0; padding:0;}
p, li{margin-bottom: 0; color:gray;}
b, strong { font-weight: 600; letter-spacing: 0.3px;}
a, a:focus, a:hover {  outline: none;  text-decoration: none;  color: gray;-webkit-transition: all .3s;  -moz-transition: all .3s;  -ms-transition: all .3s; -o-transition: all .3s; transition: all .3s;}
h4 { font-family: Poppins; font-size: 16px;  font-weight: 600;}

.arrow-up span {position: relative;height: 4px;width: 20px;display: block;margin-bottom: 23px; line-height: 1;}
.arrow-up span:before {content: ''; position: absolute;  top: 3px;  left: 0;  height: 100%;  width: 50%;  background-color: #10dfe7;
  -webkit-transform: skew(0deg, 150deg);  -moz-transform: skew(0deg, 150deg);  -ms-transform: skew(0deg, 150deg);  -o-transform: skew(0deg, 150deg);
  transform: skew(0deg, 150deg);
}

.App{overflow: hidden;}

.arrow-up span:after {  content: '';  position: absolute;  top: 3px;  right: 0;  height: 100%;  width: 50%;  background-color: #10dfe7;  -webkit-transform: skew(0deg, -150deg);
  -moz-transform: skew(0deg, -150deg);  -ms-transform: skew(0deg, -150deg);  -o-transform: skew(0deg, -150deg);  transform: skew(0deg, -150deg);
}

.btn, button, input[type="submit"] {
  display: inline-block;  font-size: 14px;  line-height: 18px;  padding: 11px 19px;  color: #fff;  background-color: #10dfe7;  border: solid 2px;
  border-color: #10dfe7;  font-weight: 400;  -webkit-transition: all .3s;  -moz-transition: all .3s;  -ms-transition: all .3s;  -o-transition: all .3s;
  transition: all .3s;  -webkit-border-radius: 0;  -moz-border-radius: 0;  border-radius: 0; outline: none; border-radius:5px;
}
.btn:hover, button:hover, input[type="submit"]:hover, .btn:focus, button:focus, input[type="submit"]:focus {
  color: #10dfe7 !important;  background-color: transparent;  border-color: #10dfe7;  outline: none;
}

