header .container-fluid { padding-right: 0;}
.top-header {background-color: #222; color: #fff; padding:10px 0;}
.header-contat button {
  border: 0;
  background: transparent;
  outline: none;
  padding: 0;
  font-size: 13px;
  font-weight: 500;
}
.header-contat img {
  height: 25px;
  margin-bottom: 5px;
  height: 50px;
  width: 50px;
  /* background: #353535; */
  padding: 5px;
  /* border: 3px solid #fff; */
}
.header-contat button span{ display: inline-block; text-transform: uppercase;}
.header-contat button:focus { color: #fff !important;}


header .nav-link {color: #000 !important;}
.logo img { height:100px;}
.ml-auto {margin-left: auto;}
.menu {padding: 0;}
.navbar {align-items: normal !important;}
.navbar-collapse {align-items: normal !important;}
.navbar-nav{align-items: center;}
.header-contat { flex-wrap: wrap; background:#10dfe7; align-items: center; display: flex; padding:0 0 0 20px;  position: relative; justify-content: center; margin:0 0 0 10px;}
.header-contat div {display: flex; justify-content: center; flex-wrap: wrap;}

.header-contat button::after {  display: none;}

.header-contat {
    flex-wrap: wrap;
    background: #10dfe7;
    align-items: center;
    display: flex;
    padding: 0 0 0 20px;
    position: relative;
    justify-content: center;
    margin: 0 0 0 10px;
}

.header-contat button i {
    height: 45px;
    width: 45px;
    line-height: 44px;
    background: #0ecbd3;
    border-radius: 100%;
    margin-right: 5px;
}


.header-contat p {width: 100%; text-align: center; font-size: 20px; font-weight: bold; color: #fff;  margin: 5px 0 0;}
