header a.nav-link { font-size: 13px; font-weight: 400;  padding: 0 15px !important; letter-spacing: 0.5px;}
.full-nav { position: static;}
.full-nav .dropdown-menu.show {width: 100%; padding: 20px;}
.full-menu-box h3 { font-size: 18px;  font-weight: 700;  border-bottom: 1px solid #222;  padding: 0 0 15px;  margin: 0 0 10px; color:#222;text-transform: uppercase;}
.full-menu-box a { padding: 10px 0; color:#111}
.full-menu-box a i {color: #10dfe7; padding: 0 5px 0 0;}
.top-left-side a {color: #fff; font-size: 12px;  padding-right: 20px;}
.top-left-side button {background: transparent !important; border: 0 !important; width: auto !important; font-size: 12px; padding: 0 !important; outline: none !important;}
.top-left-side button img {height: 20px; margin-right: 5px;}
.top-left-side { display: flex; align-items: center;}

.top-left-side a i {color: #10dfe7; padding-right: 5px;}
.top-header-row { display: flex; flex-wrap: wrap; justify-content: space-between; align-items: center;}
.top-right-side i {color: #ffffff; font-size: 14px; padding-left: 20px;}

.bottom-header { position: relative;}
.bottom-header:after {
    position: absolute;
    content: '';
    background: #10dfe7;
    right: 0;
    width:15%;
    height: 100%;
    top: 0;
    z-index: -1;
}

.top-left-side .dropdown a img {height: 20px; margin-right: 5px;}
.top-left-side .dropdown a  {color:#222}

header .nav-link .x-menu-text {
  position: relative;
}
header .nav-link:hover .x-menu-text:before {
    content: '';
    position: absolute;
    top: -18px;
    left: 50%;
    height: 4px;
    width: 10px;
    margin-left: -10px;
    background-color: #10dfe7;
    -webkit-transform: skew(0deg, 150deg);
    -moz-transform: skew(0deg, 150deg);
    -ms-transform: skew(0deg, 150deg);
    -o-transform: skew(0deg, 150deg);
    transform: skew(0deg, 150deg); 
}

header .nav-link:hover .x-menu-text:after {
    content: '';
    position: absolute;
    top: -18px;
    left: 50%;
    height: 4px;
    width: 10px;
    background-color: #10dfe7;
    -webkit-transform: skew(0deg, -150deg);
    -moz-transform: skew(0deg, -150deg);
    -ms-transform: skew(0deg, -150deg);
    -o-transform: skew(0deg, -150deg);
    transform: skew(0deg, -150deg);
} 

header .nav-link{position: relative;}
.header-contat button:hover {color: #fff;}


header .nav-link:hover:before {
  background-color: #10dfe7;
  content: "";
  display: block;
  height: 4px;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  position: absolute;
  right: 0;
  top: 70px;
  width: 70%;
}
header .navbar-nav .nav-link {
  padding-left: 0;
  padding-right: 0;
  height: 103px;
  display: flex;
  align-items: center;
  position: relative;
}

.btn-check:active+.btn-success:focus, .btn-check:checked+.btn-success:focus, .btn-success.active:focus, .btn-success:active:focus, .show>.btn-success.dropdown-toggle:focus {
    box-shadow: none !important;
}

header .dropdown .dropdown-menu {
    transition: all 0.4s;
    overflow: hidden;
    transform-origin: top center;
    transform: scale(1, 0);
    display: block 
  }
  .dropdown:hover .dropdown-menu {  transform: scale(1);  }
  header .dropdown:hover>.dropdown-menu {  display: block;}
  .header-contat:after {
    content: '';
    display: block;
    background-color: #10dfe7;
    width: 1000%;
    height: 100%;
    position: absolute;
    left: 100%;
    top: 0;
}
header .dropdown-menu[data-bs-popper] {
  top: 100%;
  left: 0;
  margin-top: 0.125rem;
  width: 300px;
  border: 0;
  border-radius: 0;
  box-shadow: none;
  border-top: 1px solid #f8f8f8;
  padding: 15px 15px;
  border-bottom: 3px solid #10dfe7;
}



header .dropdown-item{border-bottom: 1px solid #f8f8f8; padding-left:0; -webkit-transition: all .3s;  -moz-transition: all .3s;  -ms-transition: all .3s;  -o-transition: all .3s;
  transition: all .3s; }

header .dropdown-item:hover{padding-left:5px; color:#10dfe7;background:transparent;}
header .dropdown-menu .dropdown-item:first-child { padding-top: 0;}
header .dropdown-menu .dropdown-item:last-child{border-bottom:0; padding-bottom:0;}
.header-contat .btn-main:hover { color: #fff !important;}
.header-contat button  i:hover { color: #fff;}



@media screen and  (max-width: 991px) 
{
.bottom-header:after{display: none;}  
.navbar-toggler-icon {filter: brightness(0%); outline: none;}
button:focus:not(:focus-visible) {outline: 0; border: 0; box-shadow: none !important;}
div#responsive-navbar-nav { background: #222;}
header .navbar-nav .nav-link { width: 100%; background: #212121; color: #fff !important; text-align: left;}
.single-nav.nav-item.dropdown { width: 100%;}
header .navbar-nav .nav-link { padding-left: 0;  padding-right: 0;  height:45px;  display: flex;  align-items: center;  position: relative; border-bottom: 1px solid #444;}
header .dropdown .dropdown-menu {overflow: hidden; transform-origin: inherit; transform: none; display: none; width:100%;}
.navbar-nav .dropdown-menu {position: relative;}
header .nav-link .x-menu-text:after{display: none;}
header .nav-link .x-menu-text:after{display: none;}
header .nav-link:hover .x-menu-text:before{display:none}
.top-left-side button{display: none;}
.top-left-side :nth-child(2) { display: none;}
.logo img { height: 70px;}

.header-contat.mobiew-view {
  display: flex !important;
  padding: 3px;
  border-radius: 10px;
  width: 170px;
  position: absolute;
  right: 0;
  top: 15px;
}

header .navbar-dark .navbar-brand{display: none;}
.menu-row { display: flex;  justify-content: space-between; padding: 5px 0;}
.header-contat .desktop-view { display: none !important; }
.header-contat.mobiew-view :after{display: none;}
.mobiew-view-only {display: inline !important; padding: 0 10px; border-radius: 5px;}
.header-contat:after{display: none !important;}
header .header-contat button span { color: #222; }
header .header-contat button i { background: #fafafa !important; border-radius: 100%;  margin-right: 0; color: #222 !important;}

}

@media screen and  (max-width: 676px) 
{
.logo img { height:60px;}

}


